
"use client";

import { useState } from "react";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import { useToast } from "@/hooks/use-toast";
import { PageHeader } from "@/components/page-header";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card";
import { Form, FormControl, FormDescription, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Checkbox } from "@/components/ui/checkbox";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { createDifferentiatedMaterialsAction } from "./actions";
import type { CreateDifferentiatedMaterialsOutput } from "@/ai/flows/create-differentiated-materials";
import { Loader2, UploadCloud, Download, Save, X } from "lucide-react";
import Image from 'next/image';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";

const gradeLevels = Array.from({ length: 10 }, (_, i) => ({
  id: `Grade ${i + 1}`,
  label: `Grade ${i + 1}`,
}));

const subjectsByGrade: Record<string, string[]> = {
    "Grade 1": ["Language", "Mathematics", "Environmental Science"],
    "Grade 2": ["Language", "Mathematics", "Environmental Science"],
    "Grade 3": ["Language", "Mathematics", "Environmental Science", "Social Studies"],
    "Grade 4": ["Language", "Mathematics", "Environmental Science", "Social Studies"],
    "Grade 5": ["Language", "Mathematics", "Environmental Science", "Social Studies"],
    "Grade 6": ["English", "Hindi", "Mathematics", "Science", "Social Science"],
    "Grade 7": ["English", "Hindi", "Mathematics", "Science", "Social Science"],
    "Grade 8": ["English", "Hindi", "Mathematics", "Science", "Social Science"],
    "Grade 9": ["English", "Hindi", "Mathematics", "Science", "Social Science"],
    "Grade 10": ["English", "Hindi", "Mathematics", "Science", "Social Science"],
};

const lessonsBySubject: Record<string, string[]> = {
    "Mathematics": ["Numbers", "Addition and Subtraction", "Multiplication", "Division", "Geometry", "Measurement"],
    "Science": ["Living and Non-living things", "Plants", "Animals", "Human Body", "Water", "Air"],
    "Social Studies": ["My Family", "Festivals", "Our Helpers", "Our Country"],
    "English": ["Alphabet", "Vowels and Consonants", "Nouns", "Verbs", "Sentences"],
};


const worksheetsSchema = z.object({
  photos: z.array(z.any()).optional(),
  gradeLevels: z.array(z.string()).refine((value) => value.some((item) => item), {
    message: "You have to select at least one grade level.",
  }),
  subject: z.string().optional(),
  lessonTopic: z.string().optional(),
  numberOfQuestionsPerGrade: z.coerce.number().optional(),
}).refine(data => {
    return (data.photos && data.photos.length > 0) || (data.subject && data.lessonTopic);
}, {
    message: "Either upload photos or select a subject and lesson.",
    path: ["photos"],
});

type WorksheetsFormValues = z.infer<typeof worksheetsSchema>;

export default function DifferentiatedWorksheetsPage() {
  const { toast } = useToast();
  const [previews, setPreviews] = useState<string[]>([]);
  const [generatedWorksheets, setGeneratedWorksheets] = useState<CreateDifferentiatedMaterialsOutput['worksheets'] | null>(null);
  const [pending, setPending] = useState(false);

  const form = useForm<WorksheetsFormValues>({
    resolver: zodResolver(worksheetsSchema),
    defaultValues: {
      photos: [],
      gradeLevels: ["Grade 4"],
      numberOfQuestionsPerGrade: 5,
    },
  });

  const watchPhotos = form.watch("photos", []);
  const watchGrade = form.watch("gradeLevels");
  const watchSubject = form.watch("subject");

  const showPhotoFlow = watchPhotos.length > 0;
  
  const subjectsForGrade = watchGrade.length > 0 ? subjectsByGrade[`Grade ${watchGrade.map(g => parseInt(g.replace("Grade ",""))).sort((a,b)=>a-b)[0]}`] ?? [] : [];

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files) {
      const currentFiles = form.getValues("photos") ?? [];
      const newFiles = Array.from(files);
      form.setValue("photos", [...currentFiles, ...newFiles]);

      const newPreviews = [...previews];
      Array.from(files).forEach(file => {
        const reader = new FileReader();
        reader.onloadend = () => {
          newPreviews.push(reader.result as string);
          setPreviews([...newPreviews]); 
        };
        reader.readAsDataURL(file);
      });
      form.clearErrors("photos");
    }
  };

  const removeImage = (index: number) => {
    const currentFiles = form.getValues("photos") ?? [];
    const newFiles = [...currentFiles];
    newFiles.splice(index, 1);
    form.setValue("photos", newFiles);

    const newPreviews = [...previews];
    newPreviews.splice(index, 1);
    setPreviews(newPreviews);
  }
  
  const fileToBase64 = (file: File): Promise<string> => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result as string);
      reader.onerror = error => reject(error);
    });
  };

  async function onSubmit(data: WorksheetsFormValues) {
    setPending(true);
    setGeneratedWorksheets(null);
    try {
      const photoDataUris = data.photos ? await Promise.all(data.photos.map(file => fileToBase64(file))) : [];
      const result = await createDifferentiatedMaterialsAction({
        ...data,
        photoDataUris
      });
      if (result.worksheets && result.worksheets.length > 0) {
        setGeneratedWorksheets(result.worksheets);
      } else {
        toast({
          variant: "destructive",
          title: "Error",
          description: "Failed to generate worksheets. Please try a clearer photo or a different topic.",
        });
      }
    } catch (error) {
      toast({
        variant: "destructive",
        title: "Error",
        description: "An unexpected error occurred.",
      });
    } finally {
      setPending(false);
    }
  }

  const handleSave = () => {
    toast({ title: "Saved to My Library (Not Implemented)" });
  };
  
  const handleDownload = (content: string, grade: string) => {
    const blob = new Blob([content], { type: 'text/plain;charset=utf-8' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = `worksheet-${grade.replace(' ', '-')}.txt`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
    toast({ title: "Worksheet downloaded as a text file." });
  };
  

  return (
    <div className="space-y-6">
      <PageHeader
        title="Differentiated Worksheets"
        description="Generate worksheets from textbook photos or by selecting a topic."
      />
      <Form {...form}>
        <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8">
          <Card>
            <CardHeader>
              <CardTitle className="font-headline">Worksheet Request</CardTitle>
              <CardDescription>
                Upload photos of textbook pages, or select a topic to generate worksheets.
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <FormField
                control={form.control}
                name="photos"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Textbook Page Photos (Optional)</FormLabel>
                     <FormControl>
                      <div className="relative flex justify-center items-center w-full h-32 px-6 pt-5 pb-6 border-2 border-dashed rounded-md border-input">
                        <div className="space-y-1 text-center">
                            <UploadCloud className="w-12 h-12 mx-auto text-muted-foreground" />
                            <p className="text-sm text-muted-foreground">Tap or drag and drop to upload photos</p>
                        </div>
                        <Input
                            type="file"
                            accept="image/*"
                            multiple
                            className="absolute inset-0 w-full h-full opacity-0 cursor-pointer"
                            onChange={handleFileChange}
                          />
                      </div>
                    </FormControl>
                    <FormMessage />
                    {previews.length > 0 && (
                      <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-6 gap-4 mt-4">
                        {previews.map((src, index) => (
                           <div key={index} className="relative group">
                             <Image src={src} alt={`Preview ${index + 1}`} width={150} height={150} className="w-full h-auto object-contain rounded-md border" />
                             <Button
                               type="button"
                               variant="destructive"
                               size="icon"
                               className="absolute -top-2 -right-2 h-6 w-6 rounded-full opacity-0 group-hover:opacity-100"
                               onClick={() => removeImage(index)}
                              >
                                <X className="h-4 w-4" />
                                <span className="sr-only">Remove image</span>
                              </Button>
                           </div>
                        ))}
                      </div>
                    )}
                  </FormItem>
                )}
              />

              <FormField
                control={form.control}
                name="gradeLevels"
                render={() => (
                  <FormItem>
                    <div className="mb-4">
                      <FormLabel className="text-base">Select Grade Levels</FormLabel>
                      <FormDescription>
                        Choose one or more grades to generate worksheets for.
                      </FormDescription>
                    </div>
                    <div className="grid grid-cols-2 md:grid-cols-5 gap-4">
                    {gradeLevels.map((item) => (
                      <FormField
                        key={item.id}
                        control={form.control}
                        name="gradeLevels"
                        render={({ field }) => {
                          return (
                            <FormItem
                              key={item.id}
                              className="flex flex-row items-start space-x-3 space-y-0"
                            >
                              <FormControl>
                                <Checkbox
                                  checked={field.value?.includes(item.id)}
                                  onCheckedChange={(checked) => {
                                    return checked
                                      ? field.onChange([...(field.value ?? []), item.id])
                                      : field.onChange(
                                          field.value?.filter(
                                            (value) => value !== item.id
                                          )
                                        )
                                  }}
                                />
                              </FormControl>
                              <FormLabel className="font-normal">
                                {item.label}
                              </FormLabel>
                            </FormItem>
                          )
                        }}
                      />
                    ))}
                    </div>
                    <FormMessage />
                  </FormItem>
                )}
              />

              {!showPhotoFlow && (
                 <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <FormField
                      control={form.control}
                      name="subject"
                      render={({ field }) => (
                        <FormItem>
                            <FormLabel>Subject</FormLabel>
                            <Select onValueChange={(value) => {
                                field.onChange(value);
                                form.resetField('lessonTopic');
                            }} value={field.value}>
                            <FormControl>
                              <SelectTrigger>
                                <SelectValue placeholder="Select a subject" />
                              </SelectTrigger>
                            </FormControl>
                            <SelectContent>
                              {subjectsForGrade.map(subject => <SelectItem key={subject} value={subject}>{subject}</SelectItem>)}
                            </SelectContent>
                          </Select>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                    <FormField
                      control={form.control}
                      name="lessonTopic"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>Lesson / Topic</FormLabel>
                          <Select onValueChange={field.onChange} value={field.value} disabled={!watchSubject}>
                            <FormControl>
                              <SelectTrigger>
                                <SelectValue placeholder="Select a lesson" />
                              </SelectTrigger>
                            </FormControl>
                            <SelectContent>
                               { (lessonsBySubject[watchSubject!] ?? []).map(lesson => <SelectItem key={lesson} value={lesson}>{lesson}</SelectItem>)}
                            </SelectContent>
                          </Select>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                 </div>
              )}
              
              <FormField
                control={form.control}
                name="numberOfQuestionsPerGrade"
                render={({ field }) => (
                  <FormItem className="max-w-xs">
                    <FormLabel>Questions per Grade (Optional)</FormLabel>
                    <FormControl>
                      <Input type="number" placeholder="e.g., 5" {...field} />
                    </FormControl>
                  </FormItem>
                )}
              />

            </CardContent>
            <CardFooter>
              <Button type="submit" disabled={pending}>
                {pending && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                Generate Worksheets
              </Button>
            </CardFooter>
          </Card>
        </form>
      </Form>
      
       {pending && (
        <Card>
          <CardHeader>
            <CardTitle className="font-headline flex items-center gap-2">
              <Loader2 className="h-5 w-5 animate-spin text-primary" />
              <span>Generating Worksheets...</span>
            </CardTitle>
            <CardDescription>Sahayak is analyzing the page and creating materials. This may take a moment.</CardDescription>
          </CardHeader>
           <CardContent>
            <div className="space-y-2">
               <div className="h-4 bg-muted rounded animate-pulse w-full"></div>
               <div className="h-4 bg-muted rounded animate-pulse w-5/6"></div>
               <div className="h-4 bg-muted rounded animate-pulse w-full"></div>
               <div className="h-4 bg-muted rounded animate-pulse w-4/6"></div>
            </div>
          </CardContent>
        </Card>
      )}

      {generatedWorksheets && (
        <Card>
           <CardHeader>
            <CardTitle className="font-headline">Generated Worksheets</CardTitle>
            <CardDescription>Here are the differentiated materials for your selected grades. You can view, download, or save them.</CardDescription>
          </CardHeader>
          <CardContent>
            <Tabs defaultValue={generatedWorksheets[0].gradeLevel}>
              <TabsList>
                {generatedWorksheets.map(ws => (
                   <TabsTrigger key={ws.gradeLevel} value={ws.gradeLevel}>{ws.gradeLevel}</TabsTrigger>
                ))}
              </TabsList>
              {generatedWorksheets.map(ws => (
                <TabsContent key={ws.gradeLevel} value={ws.gradeLevel} className="mt-4">
                   <div className="space-y-4">
                    <div>
                      <h3 className="text-lg font-semibold font-headline mb-2">Worksheet</h3>
                      <pre className="whitespace-pre-wrap font-body text-base leading-relaxed bg-muted p-4 rounded-md">{ws.worksheetContent}</pre>
                    </div>
                     <div>
                      <h3 className="text-lg font-semibold font-headline mb-2">Answer Key</h3>
                      <pre className="whitespace-pre-wrap font-body text-base leading-relaxed bg-muted p-4 rounded-md">{ws.answerKey}</pre>
                    </div>
                     <div className="flex gap-2 pt-4">
                        <Button variant="outline" onClick={() => handleDownload(ws.worksheetContent + '\n\n---\n\n' + ws.answerKey, ws.gradeLevel)}><Download className="mr-2" /> Download</Button>
                        <Button variant="outline" onClick={handleSave}><Save className="mr-2" /> Save to Library</Button>
                     </div>
                   </div>
                </TabsContent>
              ))}
            </Tabs>
          </CardContent>
        </Card>
      )}

    </div>
  );
}
